//@import 'main-fonts'
@font-face {
	font-family: 'KeepCalm-Medium';
	src: url('../fonts/KeepCalm-Medium.ttf'); }

$white-color: #ffffff;
$black-color : #000000;
$deep-red-color: #a01d1c;
$light-red-color: #f05a41;

.btn-red {
	background: $deep-red-color;
	color: white;
	padding: 15px 25px;
	border-radius: 0;
	&:hover {
		background: #f05a41;
		color: #ffffff; } }

.red-bg {
	background: $deep-red-color; }

html body {
	font-size: 14px;
	font-family: KeepCalm-Medium;
	color: $black-color;
	h1, h2, h3, h4, h5, h6, p, .btn, a {
		font-family: KeepCalm-Medium; } }

.container {}
//width: 2176px

.no-padding {
	padding: 0; }

.main-header {
	background: url("../img/bg1.jpg") no-repeat center center;
	.navbar {
		.nav > li {
			padding-left: 20px;
			&.dropdown {
				padding-left: 0;
				color: #ffffff;
				ul.dropdown-menu {
					min-width: 150px;
					border-radius: 0;
					padding-top: 0;
					border: 0;
					li {
						&:first-child {
							background: #ededed;
							a {
								font-weight: bold;
								color: $black-color; } }
						a {
							font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
							padding: 10px 10px;
							font-size: 0.6em; } } } }
			&:first-child {
				background: url("../img/menu-bg-item-1.png") no-repeat left center; }
			&:nth-of-type(2) {
				background: url("../img/menu-bg-item-2.png") no-repeat left center; }
			&:nth-of-type(3) {
				background: url("../img/menu-bg-item-3.png") no-repeat left center; }
			&:nth-of-type(4) {
				background: url("../img/menu-bg-item-4.png") no-repeat left center; }
			& > a {
				text-transform: uppercase;
				color: #ffffff;
				font-size: 0.8em;
				&:hover {
					text-decoration: underline;
					background: none; } } } }


	.header-main-info {
		display: table;
		height: 450px;
		width: 100%;
		text-align: center;
		.inner {
			display: table-cell;
			vertical-align: middle;
			color: $white-color;
			h2 {
				font-size: 4em;
				font-family: KeepCalm-Medium; }
			h6 {
				font-size: 1em;
				margin: 30px 0; }
			.btn {
				font-size: 1em; } } }
	.search-wrapper {
		background: rgba(0,0,0,0.5);
		padding: 20px 0;
		.input-group {
			width: 90%;
			margin: 0 auto;
			.form-control {
				height: 45px; }
			.btn, input {
				border-radius: 0;
				height: 45px;
				font-size: 1em;
				border: 4px solid #000000; }
			.input-group-btn {
				font-size: 1em; }
			input {
				border-right: none; }
			.btn {
				background: $deep-red-color;
				color: white;
				padding:  6px 27px;
				border-left: none;
				font-size: 1em;
				&:hover, &:focus, &.focus, &.active {
					background: $light-red-color; } } } } }

.main {
	background: url("../img/bg2.jpg") no-repeat center top #d2d2d2;
	padding-top: 25px;
	h4 {
		font-size: 1.4em; }
	.main-numbers {
		text-align: center;
		h3 {
			font-size: 2.7em;
			color: #000000; }
		.numbers-item {
			margin: 35px 0;
			font-family: KeepCalm-Medium;
			.number {
				font-size: 7em;
				color: $deep-red-color;
				display: block;
				.comma {
					color: #000000; } }
			.number-description {
				font-size: 2.5em;
				color: #000000; } } }

	.recently-added-block, .recently-collected-block {
		border-top: #1f1f1f solid 5px;
		padding: 50px 0;
		text-align: center;
		h4 {
			margin-bottom: 35px;
			font-size: 1.8em;
			text-transform: uppercase; } }
	.cards {
		.item-wrapper {
			margin: 0 auto;
			margin-bottom: 25px;
			width: 255px;
			height: 315px;
			position: relative;
			&:hover {
				cursor: pointer; }
			.decks-description {
				display: none;
				width: calc(100% - 30px);
				padding: 10px 0;
				background: rgba(210,210,210, 0.5);
				position: absolute;
				bottom: 0;
				text-align: center;
				.deck-title {
					display: block;
					margin-bottom: 15px; }
				.collection-deck-icon {
					background: #e5e5e5;
					display: inline-block;
					width: 30px;
					height: 30px;
					line-height: 30px;
					margin-right: 10px;
					&:hover {
						background: $light-red-color; }
					.fa {
						color: $white-color; } }
				.collection-deck-success {
					background: #008000; } } }
		.back-img {
			display: none; }
		.user {
			width: 80%;
			margin: 0 auto;
			text-align: left;
			.img-circle {
				width: 50px;
				height: 50px;
				margin-right: 15px; }
			.user-name {
				display: block;
				text-transform: uppercase;
				font-size: 0.9em; }
			.time {
				display: block;
				font-style: italic;
				font-size: 0.9em; } } }
	.recently-collected-block {} }
