#inspirqation-awits {
	background: url("../img/bg3.jpg") no-repeat center center;
	height: 650px;
	width: 100%;
	display: table;
	.inner {
		display: table-cell;
		vertical-align: middle;
		text-align: center;
		color: white;
		h3 {
			font-size: 4em; }
		h6 {
			font-size: 1em;
			margin: 20px 0; }
		.btn {
			font-size: 1em; } } }

.main-footer {
	background: #212121;
	color: white;
	padding: 25px 0 75px 0;
	.footer-menu {
		list-style: none;
		margin-bottom: 35px;
		padding: 0;
		li {
			font-size: 1em;
			a {
				color: white;
				text-decoration: none; }
			&:hover {
				text-decoration: underline; } } }
	.copyright {
		font-size: 0.7em; }

	.follow-us-block {
		h5 {
			font-size: 1em;
			text-transform: uppercase;
			margin-bottom: 15px; }
		i.fa {
			font-size: 2em;
			margin-right: 10px;
			color: white;
			&:hover {
				color: $light-red-color; } } }

	.newsletter-block {
		h5 {
			font-size: 1em;
			text-transform: uppercase;
			margin-bottom: 15px; }
		.newsletter-description {
			font-size: 1em;
			display: block;
			margin: 15px 0; }
		.input-group {
			.form-control {
				height: 35px; }
			.input-group-btn {
				font-size: 1em; }
			.btn, input {
				border-radius: 0;
				height: 35px;
				font-size: 1em; }
			.btn {
				background: $light-red-color;
				color: white;
				padding:  6px 25px; } } } }


/*autocomplete inputs*/
.ui-menu {
	border: 2px solid #979797;
	border-top: 0;
	list-style: none;
	padding-left: 0;
	width: 300px;
	.ui-menu-item {
		background: #d2d2d2;
		padding: 5px; } }

.ui-helper-hidden-accessible {
	display: none; }

/*addNewDeck*/
#app-layout {}
//background: red

.hide {
	display: none; }

#addNewDeck {
	background: #f2f2f2;
	.box-images {
		background: url("../img/plus.png") center center no-repeat rgba(0,0,0,0.2);
		min-height: 350px;
		text-align: center;
		padding-top: 30%;
		&:hover {
			cursor: pointer; } }
	.header-add-deck {
		padding-top: 30px;
		padding-bottom: 30px;
		input.form-control {
			background: none;
			border: 2px solid #979797;
			margin-top: 10px; } }
	#title-add-deck {
		color: white;
		background: #ac1e1e;
		.tab-item {
			padding: 30px; } }
	.main {
		padding: 30px 0;
		background: #d2d2d2;
		color: #000000;
		textarea {

			background: none;
			margin-top: 10px;
			margin-bottom: 30px;
			box-shadow: none;
			border-left: none;
			border-right: none;
			border-top: none;
			border-bottom: 2px solid black;
			border-radius: 0; }
		input.form-control, select.form-control {
			background: none;
			border: 2px solid #979797;
			margin-top: 10px; }
		.btn .fa {
			margin-right: 7px; } } }

.user-header {
	background: #f2f2f2;
	.user-bg-header {
		width: 100%;
		height: 450px; }
	.user-info {
		padding: 30px 0 50px 0;
		font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
		.user-name {
			font-family: KeepCalm-Medium;
			font-size: 2.4em;
			text-transform: uppercase; }
		.rank {
			font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
			font-size: 1.3em;
			text-transform: uppercase; }
		.bio {
			font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
			font-size: 1.3em; }
		.user-img {
			width: 150px;
			height: 150px;
			float: left;
			margin-right: 30px; } } }
.user-collections-title {
	padding: 30px 0;
	color: $white-color; }
.user-collections {
	background: #d2d2d2; }
